.d_block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 200px;
    min-height: 50px;

    border-radius: 5px;
    border: 1px #ddd solid;
    margin: 10px;

    position: absolute;
}

.d_block:hover {
    border-color: black;
}

.d_block__title {
    width: 100%;

    text-align: center;
    margin: 0;
    padding: 15px;

    font-weight: bold;
    font-size: 110%;

    cursor: pointer;

    background-color: #eee;
    box-sizing: border-box;
}

p.d_answer {
    border: 1px #ddd solid;
    padding: 5px;
    border-radius: 5px;

    cursor: pointer;
}

p.d_answer:hover {
    border-color: #000;
}

.d_block__answer {
    display: flex;
    flex-direction: row;
    width: 100%;

    padding: 10px;
    box-sizing: border-box;
}

.d_block__answer > input {
    flex: 1;
    width: inherit;
}

.d_block__answer > div {
    width: 20px;
    height: 20px;
    border-radius: 50%;

    margin-left: 10px;

    /*border: 1px solid #dddddd;*/
    background-color: #dddddd;

    cursor: pointer;
}

.d_block__answer > div:hover {
    background-color: #ccc;
}

.d_block__answer > div.d_answer__selected {
    background-color: red;
}

.d_block__answer > div.d_answer__selected:hover {
    background-color: darkred;
}

.d_block__answer > div.d_answer__filled {
    background-color: #456;
}

.d_block__answer > div.d_answer__filled {
    background-color: #345;
}